import { LitElement, html, css } from 'lit'
import { customElement, property } from 'lit/decorators.js'

type MapWrapperMapType = 'kakao' // | 'naver' | 'google' | 'openstreetmap'

@customElement('map-wrapper')
export class MapWrapper extends LitElement {

  @property()
  type: MapWrapperMapType = 'kakao'


  override render() {
    return html`
      <script type="text/javascript" src="//dapi.kakao.com/v2/maps/sdk.js?appkey=00b619a00bcee1f2b65b81fee4fb7420&libraries=services,clusterer,drawing"></script>
      <div id="map-wrapper"
        style="width: 500px; height: 500px;"
      >loading</div>
    `
  }

  override firstUpdated() {
    const map = new kakao.maps.Map(
      this.shadowRoot!.getElementById('map-wrapper') ?? new HTMLDivElement(),
      {
        center: new kakao.maps.LatLng(33.450701, 126.570667),
        level: 3
      }
    )
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'map-wrapper': MapWrapper
  }
}
